/**
 * Sử dụng library reselect để viết lồng các selector : npm add reselect
 */
import { createSelector } from "@reduxjs/toolkit";
// các selector
export const todoListSelector = (state) => {
  localStorage.setItem('todo',JSON.stringify(state.todoList));
  return state.todoList;
};
export const searchSelector = (state) => state.filters.search;
export const statusSelector = (state) => state.filters.status;
export const prioritySelector = (state) => state.filters.priority;

// Sử dụng reselector để thao tác với các selector phụ thuộc
export const todoListRemainingSelector = createSelector(
  todoListSelector,
  statusSelector,
  prioritySelector,
  searchSelector,
  (todoList, status, priority, searchText) => {
    console.log(todoList);
    return todoList.filter((todo) => {
      if (status === "All")
        return (
          todo.name.includes(searchText) &&
          ((priority.length>0) ? priority.find((prio) => prio === todo.priority) : true)
        );
      return (
        todo.name.includes(searchText) &&
        (status === "Completed" ? todo.isCompleted : !todo.isCompleted) &&
        ((priority.length>0) ? priority.find((prio) => prio === todo.priority) : true)
      );
    });
  }
);
