import { Col, Row, Input, Button, Select, Tag } from 'antd';
import Todo from '../Todo';
//import dispatch redux
// Sử dụng useSelector để lắy dữ liệu từ store
import {useDispatch,useSelector} from 'react-redux';
// import {addTodoAction} from '../../redux/actions';
import {v4 as uuidv4} from 'uuid';
import { useState } from 'react';
import {todoListRemainingSelector} from '../../redux/selectors';

import todoListSlice from './todoSlice';

export default function TodoList() {
  // define dispatch
  const dispatch = useDispatch('');
  const [todoName, setTodoName] = useState('');
  const [todoPriority, setTodoPriority] = useState('Medium');
  // useSelector lấy từng phần dữ liệu truyền vào sate => state.todoList // k nên (Tạo selector trong redux/selectors)
  const todoList = useSelector(todoListRemainingSelector);

  const handleAddbuttonClick = () =>{
    dispatch(todoListSlice.actions.addTodo({
      id : uuidv4(),
      name : todoName,
      priority: todoPriority,
      isCompleted : false,
    }));

    setTodoName('');
    setTodoPriority('Medium');
  }

  const handleInputChange = (e)=>{
    setTodoName(e.target.value);
  }

  const handleSelectChange = (value) =>{
    setTodoPriority(value);
  }
  return (
    <Row style={{ height: 'calc(100% - 40px)' }}>
      <Col span={24} style={{ height: 'calc(100% - 40px)', overflowY: 'auto' }}>
        {/* <Todo name='Learn React' priority='High' />
        <Todo name='Learn Redux' priority='Medium' />
        <Todo name='Learn JavaScript' priority='Low' /> */}
        {todoList.map(todo=><Todo id={todo.id} name={todo.name} priority={todo.priority} isCompleted={todo.isCompleted} key={todo.id}/>)}
      </Col>
      <Col span={24}>
        <Input.Group style={{ display: 'flex' }} compact>
          <Input value={todoName} onChange={handleInputChange}/>
          <Select defaultValue="Medium" onChange={handleSelectChange}>
            <Select.Option value='High' label='High'>
              <Tag color='red'>High</Tag>
            </Select.Option>
            <Select.Option value='Medium' label='Medium'>
              <Tag color='blue'>Medium</Tag>
            </Select.Option>
            <Select.Option value='Low' label='Low'>
              <Tag color='gray'>Low</Tag>
            </Select.Option>
          </Select>
          <Button type='primary' onClick={handleAddbuttonClick}>
            Add
          </Button>
        </Input.Group>
      </Col>
    </Row>
  );
}
