import { Col, Row, Input, Typography, Radio, Select, Tag } from 'antd';
import { useState } from 'react';
import {useDispatch} from 'react-redux';
// import {searchTodoAction,filterStatusAction,filterPriorityAction} from '../../redux/actions';
import filtersSlice from './filtersSlice';
const { Search } = Input;

export default function Filters() {
  const [search, setSearch] = useState('');
  const [status, setStatus] = useState('All');
  const [priority, setPriority] = useState([]);
  const dispatch = useDispatch();

  const handleInputOnChange = (e) =>{
    setSearch(e.target.value);
    dispatch(filtersSlice.actions.searchTodo(e.target.value));
  }

  const handleChangeSelectStatus =(e) =>{
    setStatus(e.target.value);
    dispatch(filtersSlice.actions.filterStatus(e.target.value));
  }

  const handleChangePriority = (e) => {
    setPriority(e);
    dispatch(filtersSlice.actions.filterPriority(e));
  }
  return (
    <Row justify='center'>
      <Col span={24}>
        <Typography.Paragraph
          style={{ fontWeight: 'bold', marginBottom: 3, marginTop: 10 }}
        >
          Search
        </Typography.Paragraph>
        <Search placeholder='input search text'  onChange={handleInputOnChange}/>
      </Col>
      <Col sm={24}>
        <Typography.Paragraph
          style={{ fontWeight: 'bold', marginBottom: 3, marginTop: 10 }}
        >
          Filter By Status
        </Typography.Paragraph>
        <Radio.Group value={status} onChange={handleChangeSelectStatus}>
          <Radio value='All'>All</Radio>
          <Radio value='Completed'>Completed</Radio>
          <Radio value='Todo'>To do</Radio>
        </Radio.Group>
      </Col>
      <Col sm={24}>
        <Typography.Paragraph
          style={{ fontWeight: 'bold', marginBottom: 3, marginTop: 10 }}
        >
          Filter By Priority
        </Typography.Paragraph>
        <Select
          mode='multiple'
          allowClear
          placeholder='Please select'
          style={{ width: '100%' }}
          onChange={handleChangePriority}
        >
          <Select.Option  value='High' label='High'>
            <Tag color='red'>High</Tag>
          </Select.Option>
          <Select.Option value='Medium' label='Medium'>
            <Tag color='blue'>Medium</Tag>
          </Select.Option>
          <Select.Option value='Low' label='Low'>
            <Tag color='gray'>Low</Tag>
          </Select.Option>
        </Select>
      </Col>
    </Row>
  );
}
